import React from 'react';
import { connect } from 'react-redux';

import { setWindowSize } from '../../actions'

import ProtectedContent from '../containers/specific/protected-content';
import Authorization from '../containers/authorization';
import Header from '../views/specific/header';

import dictionary from '../../localization';
import { LocalizationProvider } from "../contexts/localization-context";

import UpdateCurtain from '../containers/specific/update-curtain';
import PingingServerCurtain from '../containers/specific/pinging-server-curtain';

import './app.css';

const App = ({ language, isLogin, gatewayid, isLoadUpdate, setWindowSize, isWaitingForServerToLoad }) => {
	const localization = dictionary(language);

	const authorization = isLogin ? null : <Authorization />;
	const protectedContent = isLogin ? <ProtectedContent /> : null;
	const updateSpinner = isLoadUpdate ? <UpdateCurtain /> : null;
	const pingingServerCurtain = isWaitingForServerToLoad ? <PingingServerCurtain /> : null;

	// TODO: remake windowHeight
	const windowHeight = document.documentElement.clientHeight;
	setWindowSize(0, windowHeight);

	window.onresize = () => {
		const resizeWindowHeight = document.documentElement.clientHeight;
		setWindowSize(0, resizeWindowHeight);
	};

	return (
        <LocalizationProvider value={localization}>
			{updateSpinner}
			{pingingServerCurtain}
			<div className = "container app">
				<Header
					gateway_ID={gatewayid}
					isLogin={isLogin}
					deviceName={localization.dictionary('about.device.name.value')}
					localization={localization.localization}
				/>
				{authorization}
				{protectedContent}
			</div>
        </LocalizationProvider>
	)
}

const mapStoreToProps = ({ language, isLogin, gatewayid, isLoadUpdate, isWaitingForServerToLoad }) => {
	return {
		language,
		isLogin,
		gatewayid,
		isLoadUpdate,
		isWaitingForServerToLoad
	}
}

const mapDispatchToProps = {
	setWindowSize
}

export default connect(mapStoreToProps, mapDispatchToProps)(App);
