import React from 'react';

import './update-panel.css';

const UpdatePanel = ({ onUpdate, updateData, localization, localizationPrefix }) => {
    const { dashboardVersion, managerVersion } = updateData;
    //const { dashboardUpdate, managerUpdate } = localization.server;

    const dashboardText = dashboardVersion.isAvailableUpdate ? localization(localizationPrefix + 'server.dashboardUpdate') + ': ' + dashboardVersion.message + ' (' + dashboardVersion.date + ')' : null;
    const managerText = managerVersion.isAvailableUpdate ? localization(localizationPrefix + 'server.managerUpdate') + ': ' + managerVersion.message + ' (' + managerVersion.date + ')' : null;
    let br = null;
    if (dashboardVersion.isAvailableUpdate && managerVersion.isAvailableUpdate) {
        br = <br />;
    }
    return (
        <div className = "about-view-update-panel container-fluid">
            <div className="info-circle">
                <i className="fa fa-exclamation-circle" aria-hidden="true"></i>
            </div>
            <div className="pnl">
                <button className="button" onClick = {onUpdate}>
                    <i className="fa fa-refresh" aria-hidden="true"></i>
                    {localization(localizationPrefix + 'buttons.update')}
                </button>
                {dashboardText}{br}{managerText}
                <div style={{clear: 'both'}}></div>
            </div>
		</div>
    )
}

export default UpdatePanel;
