import React, { Component } from 'react';
import { connect } from 'react-redux';
import compose from '../../../../utils/compose';
import { withApiService, withLocalization, withLoraLogsRequester } from '../../../hoc';

import {
    fetchLoraLogsConfigs,
    toggleAutoFetchLoraLogs,
    onSaveLoraLogToFile
} from '../../../../actions';

import Layout from '../../../views/pages/lora-logs/layout';
import LogArea from '../../../views/pages/lora-logs/log-area';
import LoraLogsDashboardArea from '../../../views/pages/lora-logs/dashboard-area';

class LoraLogsPage extends Component {
    constructor () {
        super();
        this.state = {
            isScrolling: true
        }
    }

    componentDidMount () {

    };

    componentWillUnmount () {

    };

    toggleIsScrolling = () => {
        this.setState(({isScrolling}) => {
            return {isScrolling: !isScrolling}
        })
    };

    render () {
        const {
            dictionary: localization,

            loginToken,
            height,

            data,
            dataState,
            error,
            isAutoLogChecking,

            fetchLoraLogsConfigs,
            toggleAutoFetchLoraLogs,
            intervalId,

            LoraLogsRequester,
            ApiService
        } = this.props;

        const {
            isScrolling
        } = this.state;

        if (dataState === 'EMPTY') {
            fetchLoraLogsConfigs(loginToken, ApiService);
        }

        const logArea = (
            <LogArea height={height} isScrolling={isScrolling}>
                {data}
            </LogArea>
        );

        const loraLogsDashboardArea = (
            <LoraLogsDashboardArea
                toggleAutoFetchLoraLogs={()=>toggleAutoFetchLoraLogs(loginToken, ApiService, LoraLogsRequester, isAutoLogChecking, intervalId)}
                isAutoLogChecking={isAutoLogChecking}
                toggleIsScrolling={this.toggleIsScrolling}
                isScrolling={isScrolling}
                saveAction={()=>onSaveLoraLogToFile(data)}

                localization={localization}
                localizationPrefix="loraLogs."
            />
        );

        return (
            <Layout
                title={localization("loraLogs.title")}
                dashboardArea={loraLogsDashboardArea}
                logArea={logArea}
                error={error}
            />
        )
    }
};

const mapStoreToProps = ({ loginToken, height, loraLogs }) => {
    const {
        data,
        dataState,
        error,
        isAutoLogChecking,
        intervalId
    } = loraLogs;
    return {
        loginToken,
        height,

        data,
        dataState,
        error,
        isAutoLogChecking,
        intervalId
    }
};

const mapDispatchToProps = {
    fetchLoraLogsConfigs,
    toggleAutoFetchLoraLogs
};

export default compose(
    connect(mapStoreToProps, mapDispatchToProps),
    withApiService,
    withLoraLogsRequester,
    withLocalization
)(LoraLogsPage);
