class LoraLogRequester {
    constructor () {
        this.isRequesting = false;
        this.timerId = null;
    }

    startRequesting (fetchRepeatedLoraLogsConfigs, loginToken, ApiService, dispatch) {
        if (!this.isRequesting) {
            this.isRequesting = true;

            const repeater = () => {
                if(this.isRequesting) {
                    fetchRepeatedLoraLogsConfigs(loginToken, ApiService, ()=>{
                        clearTimeout(this.timerId);
                        this.timerId = setTimeout(repeater, 5000);
                    })(dispatch)
                }
            }

            repeater();
        }
    }

    stopRequesting () {
        this.isRequesting = false;
        clearTimeout(this.timerId);
    }
}

export default LoraLogRequester;
