import React from 'react';
import { connect } from 'react-redux';

import { withLocalization } from '../../../hoc';

import MainMenuLayer from '../../../views/specific/main-menu';

const MainMenu = ({
    isSupport3G,
    globalConfIsChanged,
    networkConfIsChanged,
    apn3GConfIsChanged,
    frequencyConfIsChanged,
    gpsLoraConfigsChanged,
    otherLoraConfigsChanged,
    isAvailableUpdate,
    isAutoLogChecking,

    dictionary: localization,
    store
}) => {
    return (
        <MainMenuLayer
            isSupport3G={isSupport3G}
            globalConfIsChanged={globalConfIsChanged}
            networkConfIsChanged={networkConfIsChanged}
            apn3GConfIsChanged={apn3GConfIsChanged}
            frequencyConfIsChanged={frequencyConfIsChanged}
            gpsLoraConfigsChanged={gpsLoraConfigsChanged}
            otherLoraConfigsChanged={otherLoraConfigsChanged}

            isAvailableUpdate={isAvailableUpdate}
            isAutoLogChecking={isAutoLogChecking}

            localization={localization}
            store={store}
        />
    )
}

const mapStoreToProps = (store) => {
    // TODO: store uses for show store for developing, remove store
    const { isSupport3G, global_conf, network_conf, conf_3G, frequency_conf, gpsLoraConfigs, otherLoraConfigs, about={}, loraLogs } = store;
	const { isChanged: globalConfIsChanged } = global_conf;
    const { isChanged: networkConfIsChanged } = network_conf;
    const { isChanged: apn3GConfIsChanged } = conf_3G;
    const { isChanged: frequencyConfIsChanged } = frequency_conf;
    const { isChanged: gpsLoraConfigsChanged } = gpsLoraConfigs;
    const { isChanged: otherLoraConfigsChanged } = otherLoraConfigs;

    const {isAutoLogChecking} = loraLogs;
    const {server = {}} = about;
    const {update = {}} = server;

	return {
        isSupport3G,
		globalConfIsChanged,
        networkConfIsChanged,
        apn3GConfIsChanged,
        frequencyConfIsChanged,
        gpsLoraConfigsChanged,
        otherLoraConfigsChanged,
        isAvailableUpdate: update.isAvailableUpdate,
        isAutoLogChecking,

        store
	}
}

export default connect(mapStoreToProps)(withLocalization(MainMenu));
