import React from 'react';

import PageFrame from '../../../common/page-frame';
import ErrorIndicator from '../../../common/error-indicator';

import './layout.css';

const Layout = ({
    title,
    dashboardArea,
    logArea,
    error
}) => {
    const errorIndicator = error ? <ErrorIndicator error={error} /> : null;
    return (
        <PageFrame name={title}>
            <div className="lora-logs-layout">
                {dashboardArea}
                {errorIndicator}
                {logArea}
            </div>
        </PageFrame>
    )
};

export default Layout;
