import React from 'react';
import { connect } from 'react-redux';
import compose from '../../../../utils/compose';

import { isObjectValid } from '../../../../utils/page-data-validators'
import { withApiService, withLocalization } from '../../../hoc';

import {
    fetchGpsLoraConfigs,
    sendGpsLoraConfigs,
    editGpsLoraConfigsAction,
    gpsLoraConfigsOpenConfirm,
    gpsLoraConfigsCloseConfirm
} from '../../../../actions';

import Spinner from '../../../views/common/spinner';
import ConfirmForm from '../../../views/common/confirm-form';

import Layout from '../../../views/pages/gps-lora-configs/layout';
import Form from '../../../views/pages/gps-lora-configs/form';
import Buttons from '../../../views/pages/gps-lora-configs/buttons';


const GpsLoraConfigs = ({
    loginToken,

    data,
    currentData,
    dataState,
    isDataValid,
	isNeedConfirm,
	confirmText,
	confirmAction,
    error,

    fetchGpsLoraConfigs,
    sendGpsLoraConfigs,
    editGpsLoraConfigsAction,
    gpsLoraConfigsOpenConfirm,
    gpsLoraConfigsCloseConfirm,

    ApiService,
    dictionary
}) => {

	const onSetConfigsConfirmData = {
		confirmText: dictionary("gpsLoraConfigs.confirmReqests.onSave"),
		confirmAction: () => sendGpsLoraConfigs(loginToken, ApiService, data)
	}
	const onReRequestConfirmData = {
		confirmText: dictionary('gpsLoraConfigs.confirmReqests.onReRequest'),
		confirmAction: () => fetchGpsLoraConfigs(loginToken, ApiService)
	}

    const onChangeLabel = (field, value) => {
        editGpsLoraConfigsAction(field, value);
    }

	const sendDataAction = () => {
		if (isObjectValid(isDataValid)) {
			gpsLoraConfigsOpenConfirm(onSetConfigsConfirmData);
		} else {
			alert(dictionary('confirm.notValidFields'));
		}
	}

    let content = null;
    switch (dataState) {
        case 'EMPTY': {
            fetchGpsLoraConfigs(loginToken, ApiService);
            break;
        }
        case 'LOADING': {
            content = <Spinner />
            break;
        }
        case 'READY': {
            content = <Form
                localization={dictionary}
                localizationPrefix="gpsLoraConfigs."

                data={data}
                currentData={currentData}
                isDataValid={isDataValid}
                onChangeLabel={onChangeLabel}
                onPressEnter={sendDataAction}
                isActive={!isNeedConfirm}
            />
            break;
        }
        case 'ERROR': {
            content = null
            break;
        }
        default: content = null
    }

    let buttons = null;
	if (isNeedConfirm) {
		buttons = <ConfirmForm
            confirmText={confirmText}
            onConfirm={confirmAction}
            onCancel={()=>gpsLoraConfigsCloseConfirm()}
        />;
	} else {
		buttons = <Buttons
            localization={dictionary}
            localizationPrefix="gpsLoraConfigs."

			dataState={dataState}
			sendData={sendDataAction}
		    reRequestData={()=>gpsLoraConfigsOpenConfirm(onReRequestConfirmData)}
		/>;
	}

    return (
        <Layout
            title={dictionary('gpsLoraConfigs.title')}
            content={content}
            error={error}
            buttons={buttons}
        />
    )
};


const mapStoreToProps = ({loginToken, gpsLoraConfigs}) => {
    const {
        data,
        currentData,
        dataState,
        isDataValid,
		isNeedConfirm,
		confirmText,
		confirmAction,

        error
    } = gpsLoraConfigs;
    return {
        loginToken,

        data,
        currentData,
        dataState,
        isDataValid,
		isNeedConfirm,
		confirmText,
		confirmAction,

        error
    }
}


const mapDispatchToProps = {
    fetchGpsLoraConfigs,
    sendGpsLoraConfigs,
    editGpsLoraConfigsAction,
    gpsLoraConfigsOpenConfirm,
    gpsLoraConfigsCloseConfirm
}


export default compose(
    connect(mapStoreToProps, mapDispatchToProps),
    withApiService,
    withLocalization
)(GpsLoraConfigs);
