import React from 'react';

import PageFrame from '../../../common/page-frame';
import ErrorIndicator from '../../../common/error-indicator';

import './layout.css';

const Layout = ({
    title=null,
    content,
    error=null,
    buttons=null
}) => {
    const errorIndicator = error ? <ErrorIndicator error={error} /> : null;

    return (
        <PageFrame name={title}>
            <div className="other-lora-configs-layout">
                <div className="layout-error-indicator">
                    {errorIndicator}
                </div>
                <div className="layout-content">
                    {content}
                </div>
                <div className="layout-buttons">
                    {buttons}
                </div>
            </div>
        </PageFrame>
    )
}

export default Layout;
