import React from 'react';
import ReactDOM from 'react-dom';

import { Provider } from 'react-redux';

import App from './components/app';
import ErrorBoundary from './components/containers/error-boundary';
// TODO: dummy for api service
//import ApiService from './services/dummy-api-service';
import ApiService from './services/api-service';
import { ApiServiceProvider } from "./components/contexts/api-service-context";

import LoraLogsRequester from './services/lora-logs-requester';
import { LoraLogsRequesterProvider } from "./components/contexts/lora-logs-requester-context";

import store from './store';

const apiService = new ApiService();
const loraLogsRequester = new LoraLogsRequester();
/**
 * Create the App component configured with the Redux store and the Web socket
 * wrapped by Error boundary.
 */
ReactDOM.render(
        <Provider store={store}>
            <ErrorBoundary>
                <ApiServiceProvider value={apiService}>
                    <LoraLogsRequesterProvider value={loraLogsRequester}>

                        <App />
                    </LoraLogsRequesterProvider>
                </ApiServiceProvider>
            </ErrorBoundary>
        </Provider>,
    document.getElementById('root'));
