import React from 'react';

import BlockingCurtain from '../../../common/blocking-curtain';
import Spinner from '../../../common/spinner';

import './layout.css';

const Layout = ({
    label,
    additionalLabel = null,
    rows,
    updateState
}) => {

    return (
        <BlockingCurtain>
            <div className={'update-curtain-layout'}>
                <div className={updateState}>
                    <div className="update-curtain-label">{label}</div>
                    {(additionalLabel !== null ? <div className="additional-label">{additionalLabel}</div> : null)}
                    {(updateState === "FAILURE" ? <div className="failure-icon"><i className="fa fa-exclamation-triangle" aria-hidden="true"></i></div> : <Spinner />)}
                    {rows}
                </div>
            </div>
        </BlockingCurtain>
    )
};

export default Layout;
