import React from 'react';
import { connect } from 'react-redux';

import { withApiService } from '../../../../hoc';

import {
    onSelectItem,
    fetchFrequencyPresets,
    onSavePresetFile,
	onOpenPresetFile
} from '../../../../../actions';

import FrequencyPresetsBlock from '../frequency-presets-block';
import ErrorIndicator from '../../../../views/common/error-indicator';

const FrequencyConfigsPresetExpert = ({
    active,
    ApiService,
    localization,
    loginToken,

    listState,

    presetsDataState,
    selectedItem,
    presetsData,
    presetsDataErrors,

    currentDevicePreset,

    onSelectItem,
    error,
    fetchFrequencyPresets,
    onSavePresetFile,
	onOpenPresetFile
}) => {
    const { presets: presetLocalization } = localization.loraGlobalFrequency;

    let fileElement = [];
    if (presetsDataState.fromFile === 'READY') {
        fileElement = [{
           name: presetLocalization.fromFile.title,
           active,
           isSelected: (selectedItem === 'fromFile'),
           description: presetLocalization.fromFile.description,
           itemState: 'READY',
           onSelect: active ? () => onSelectItem('fromFile') : () => {},
           isAuxiliary: true
       }];
    }

    const manualElement = {
        name: presetLocalization.manual.title,
        active,
        isSelected: (selectedItem === 'manual'),
        isCurrent: (currentDevicePreset === 'manual'),
        description: presetLocalization.manual.description,
        itemState: 'READY',
        onSelect: active ? () => onSelectItem('manual') : () => {},
        isAuxiliary: true
    };

    let content = null;
    switch (listState) {
        case 'EMPTY': {
            fetchFrequencyPresets(loginToken, ApiService);
            break;
        }
        case 'LOADING': {
            break;
        }
        case 'READY': {
            content = (
                <FrequencyPresetsBlock
                    isActive={active}
                    onSelect={(name) => onSelectItem(name)}
                    additionalElements={[manualElement, ...fileElement]}
                    selectedItem={selectedItem}
                />
            )
            break;
        }
        case 'ERROR': {
            break;
        }
        default: {}
    }

    const errorBlock = error ? <ErrorIndicator error={error} /> : null;

    return (
        <div>
            {content}
            {errorBlock}
        </div>
    )
}

const mapStoreToProps = ({ loginToken, isLogin, frequency_presets }) => {
    const {
        listState,

        presetsDataState,
        presetsData,
        presetsDataErrors,
        selectedItem,
        currentDevicePreset,
        error
    } = frequency_presets;

    return {
        loginToken,
        listState,

        isLogin,
        presetsDataState,
        presetsData,
        presetsDataErrors,
        selectedItem,
        currentDevicePreset,
        error
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        onSelectItem: (name) => dispatch(onSelectItem(name)),

        fetchFrequencyPresets: (loginToken, ApiService) => fetchFrequencyPresets(loginToken, ApiService)(dispatch),
        onSavePresetFile: (configs) => onSavePresetFile(configs),
    	onOpenPresetFile: () => onOpenPresetFile(dispatch)
    }
}

export default connect(mapStoreToProps, mapDispatchToProps)(withApiService(FrequencyConfigsPresetExpert));
