import React from 'react';
import { connect } from 'react-redux';
import compose from '../../../../utils/compose';

import { withApiService, withLocalization } from '../../../hoc';
import { isObjectValid } from '../../../../utils/page-data-validators';

import {
    fetchDashboardAppSettings,
    sendDashboardAppSetting,
    editDashboardAppSettings,
    dashboardAppSettingsOpenConfirm,
    dashboardAppSettingsCloseConfirm
} from '../../../../actions';

import Spinner from '../../../views/common/spinner';
import ErrorIndicator from '../../../views/common/error-indicator';
import ConfirmForm from '../../../views/common/confirm-form';

import AppDashboardSettingsLayout from '../../../views/pages/dashboard-app-settings/layout';
import AppDasboardSettingsForm from '../../../views/pages/dashboard-app-settings/form';
import DashboardAppSettingsButtons from '../../../views/pages/dashboard-app-settings/buttons';

const AppDashboardSettings = ({
    loginToken,
    expertMode,

    dashboardSettings,
    fetchDashboardAppSettings,
    sendDashboardAppSetting,
    editDashboardAppSettings,
    dashboardAppSettingsOpenConfirm,
    dashboardAppSettingsCloseConfirm,

    dictionary: localization,
    ApiService
}) => {
    const localizationPrefix = 'appSettings.appDashboard.'

    const {
        data,
        dataState,
        isValid,
        error,
        isNeedConfirm,
        confirmText,
        confirmAction
    } = dashboardSettings;

    const sendConfigsConfirmData = {
		confirmText: localization(localizationPrefix + 'confirmReqests.set'),
		confirmAction: () => sendDashboardAppSetting({loginToken, ApiService, data})
	}
    const getConfigsConfirmData = {
		confirmText: localization(localizationPrefix + 'confirmReqests.get'),
		confirmAction: () => fetchDashboardAppSettings({loginToken, ApiService})
	}

    const sendData = (confirmerData, isValid) => () => {
        if (isObjectValid(isValid)) {
            dashboardAppSettingsOpenConfirm(confirmerData, isValid);
        } else {
            alert(localization(`confirm.notValidFields`));
        }
    }

    const onChange = (fieldPath) => (val) => {
        editDashboardAppSettings(fieldPath, val);
    }

    let form = null;
    switch (dataState) {
        case 'EMPTY': {
            fetchDashboardAppSettings({loginToken, ApiService});
            break;
        }
        case 'LOADING': {
            form = <Spinner />
            break;
        }
        case 'SUCCESS': {
            form = <AppDasboardSettingsForm
                localization={localization}
                localizationPrefix={localizationPrefix}
                isExpertMode={expertMode}
                isActive={!isNeedConfirm}
                data={data}
                isValid={isValid}
                onChange={onChange}
                onSubmit={sendData(sendConfigsConfirmData, isValid)}
                />;
            break;
        }
        case 'ERROR': {
            form = <ErrorIndicator error={error} />
            break;
        }
        default: {}
    }

    let buttons = null;
    if (isNeedConfirm) {
        buttons = <ConfirmForm
            confirmText={confirmText}
            onConfirm={confirmAction}
            onCancel={()=>dashboardAppSettingsCloseConfirm()}
        />
    } else {
        buttons = <DashboardAppSettingsButtons
            localization={localization}
            localizationPrefix={localizationPrefix}
            rerequestData={()=>dashboardAppSettingsOpenConfirm(getConfigsConfirmData, isValid)}
            sendData={sendData(sendConfigsConfirmData, isValid)}
        />
    }

    return (
        <AppDashboardSettingsLayout
            error={dataState !== 'ERROR' ? error : null}
            form={form}
            buttons={buttons}
            title={'#Dashboard settings'}
        />
    )
}

const mapStoreToProps = ({ loginToken, appSettings, expertMode }) => {
    const { dashboardSettings } = appSettings;
    return {
        loginToken,
        dashboardSettings,
        expertMode
    }
}

const mapDispatchToProps = {
    fetchDashboardAppSettings,
    sendDashboardAppSetting,
    editDashboardAppSettings,
    dashboardAppSettingsOpenConfirm,
    dashboardAppSettingsCloseConfirm
}

export default compose(
    withApiService,
    withLocalization,
    connect(mapStoreToProps, mapDispatchToProps)
)(AppDashboardSettings);
