import ru from './ru';
import en from './en';
import parseObjectByMask from '../utils/objects-utils/parse-object-by-mask.js';

const dictionary = (locale) => {
    let currentLocale = null;
    switch (locale) {
        case 'ru': {
            currentLocale = ru;
            break;
        }
        case 'en': {
            currentLocale = en;
            break;
        }
        default: {
            currentLocale = ru;
            break;
        }
    }


    const fetchText = (path) => {

        const pathPartsArray = path.split('.');

        let result = '#' + path;

        let obj = currentLocale;
        for (let i = 0; i < pathPartsArray.length; i += 1 ) {
            if (typeof obj[pathPartsArray[i]] === 'object' && obj[pathPartsArray[i]] !== null) {
                obj = obj[pathPartsArray[i]]
            } else if (typeof obj[pathPartsArray[i]] === 'string' ||  obj[pathPartsArray[i]] === null) {
                result = obj[pathPartsArray[i]];
                break;
            } else if (obj[pathPartsArray[i]] === undefined) {
                break;
            }
        };

        return result;
    }


    return {
        localization: parseObjectByMask(currentLocale, en),
        dictionary: fetchText
    };
}

export default dictionary;
