import saveFile from '../utils/save-file';

const onSaveLoraLogToFile = (data) => {
    const text = data.reduce((accum, current) => (accum + current.row), '');
    saveFile(text, "lora-logs.txt", "text/plain");
}

const fetchLoraLogsRequestAction = () => {
    return {
        type: 'FETCH_LORA_LOGS_REQUEST'
    }
};

const fetchLoraLogsSuccessAction = (data) => {
    const payload = data;
    return {
        type: 'FETCH_LORA_LOGS_SUCCESS',
        payload
    }
};

const fetchLoraLogsFailureAction = (err) => {
    const payload = err;
    return {
        type: 'FETCH_LORA_LOGS_FAILURE',
        payload
    }
};

const fetchLoraLogsRepeatedRequestAction = () => {
    return {
        type: 'FETCH_LORA_LOGS_REPEATED_REQUEST'
    }
};

const toggleLoraLog =(isAutoLogChecking) => {
    return {
        type: 'TOGGLE_AUTO_FETCH_LORA_LOGS',
        payload: {
            isAutoLogChecking
        }
    }
}

/*const loraLogRequester*/

const toggleAutoFetchLoraLogs = (loginToken, ApiService, LoraLogsRequester, isAutoLogChecking) => (dispatch) => {
    dispatch(toggleLoraLog(!isAutoLogChecking))
    if (!isAutoLogChecking) {
        LoraLogsRequester.startRequesting(fetchRepeatedLoraLogsConfigs, loginToken, ApiService, dispatch);
    } else {
        LoraLogsRequester.stopRequesting();
    }
};

const fetchLoraLogsConfigs = (loginToken, ApiService) => (dispatch) => {
	    const head = {
        cmd: 'get_lora_log',
        loginToken
    };
    dispatch(fetchLoraLogsRequestAction());
    ApiService.loraLogsRequest(head)
        .then((response) => {
			if (response.result) {
				dispatch(fetchLoraLogsSuccessAction(response.data))
			} else {
				dispatch(fetchLoraLogsFailureAction(response.msg));
			}
		})
        .catch((er) => {
			console.error("ERROR", er);
            dispatch(fetchLoraLogsFailureAction('cant_connect_to_server'));
        });
}

const fetchRepeatedLoraLogsConfigs = (loginToken, ApiService, next=()=>{}) => (dispatch) => {
	    const head = {
        cmd: 'get_lora_log',
        loginToken
    };
    dispatch(fetchLoraLogsRepeatedRequestAction());
    ApiService.loraLogsRequest(head)
        .then((response) => {
			if (response.result) {
				dispatch(fetchLoraLogsSuccessAction(response.data));
                next();
			} else {
				dispatch(fetchLoraLogsFailureAction(response.msg));
                next();
			}
		})
        .catch((er) => {
			console.error("ERROR", er);
            dispatch(fetchLoraLogsFailureAction('cant_connect_to_server'));
            next();
        });
}

export {
    fetchLoraLogsConfigs,
    fetchRepeatedLoraLogsConfigs,
    toggleAutoFetchLoraLogs,
    onSaveLoraLogToFile
}
