import checkDataDifferent from '../utils/check-data-different';
import deepMerge from '../utils/objects-utils/deep-merge'

const initialLoraConfigsStore = {
    data: {},
    currentData: {},
    isDataValid: {},
    isChanged: false,
    dataState: 'EMPTY',
    isNeedConfirm: false,
    confirmText: null,
    confirmAction: null,
    error: null
}

const otherLoraConfigsReducer = (otherLoraConfigsStore=initialLoraConfigsStore, action) => {
    switch (action.type) {
        case 'FETCH_LORA_OTHER_CONFIGS_REQUEST': {
            return {
                ...initialLoraConfigsStore,
                data: {},
                isDataValid: {},
                dataState: 'LOADING',
                error: null
            }
        }


        case 'FETCH_LORA_OTHER_CONFIGS_SUCCESS': {
            const {
                isValid,
                data
            } = action.payload;
            return {
                ...initialLoraConfigsStore,
                data: {
                    ...data
                },
                currentData: {
                    ...data
                },
                isDataValid: {
                    ...isValid
                },
                dataState: 'READY',
                error: null
            }
        }


        case 'FETCH_LORA_OTHER_CONFIGS_FAILURE': {
            return {
                ...otherLoraConfigsStore,
                data: {},
                isDataValid: {},
                dataState: 'ERROR',
                error: action.payload
            }
        }


        case 'LORA_OTHER_CONFIGS_ERROR': {
            return {
                ...otherLoraConfigsStore,
                error: action.payload
            }
        }


        case 'EDIT_LORA_OTHER_CONFIGS': {
            const newLoraConfigsStore = deepMerge(otherLoraConfigsStore, action.payload);

            const isChanged = checkDataDifferent(newLoraConfigsStore);

            return {
                ...newLoraConfigsStore,
                isChanged
            }
        }


        case 'LORA_OTHER_CONFIGS_OPEN_CONFIRM': {
            const {
                confirmText,
                confirmAction
            } = action.payload;

            return {
                ...otherLoraConfigsStore,
                isNeedConfirm: true,
                confirmText,
                confirmAction
            }
        }


        case 'LORA_OTHER_CONFIGS_CLOSE_CONFIRM': {
            return {
                ...otherLoraConfigsStore,
                isNeedConfirm: false,
            	confirmText: null,
            	confirmAction: null
            }
        }


        default: {
            return {
                ...initialLoraConfigsStore
            }
        }
    }
}

export default otherLoraConfigsReducer;
