import {
    numberValidator
} from '../type-data-validators'

const otherLoraConfigsValidator = (field, value) => {
    switch (field) {
        case 'Object.SX1301_conf.antenna_gain':
        case 'Object.gateway_conf.keepalive_interval': {
            return numberValidator(value, 8);
        }

        default: {
            return {data: value, isValid: false};
        }
    }
}

export default otherLoraConfigsValidator;
