import { deepMap } from '../utils/objects-utils/deep-map-obj';
import createObjectByFieldPathfrom from '../utils/objects-utils/create-object-by-field-path';
import otherLoraConfigsValidator from '../utils/validators/other-lora-configs-validator';

const fetchLoraOtherConfigsRequestAction = () => {
    return {
        type: 'FETCH_LORA_OTHER_CONFIGS_REQUEST'
    }
};


const fetchLoraOtherConfigsSuccessAction = (data) => {
    const isValid = deepMap(data, (key, val, path) => {
		const result = otherLoraConfigsValidator(path, val).isValid;
		return (result);
	});

	const payload = {
		data,
		isValid
	}

    return {
        type: 'FETCH_LORA_OTHER_CONFIGS_SUCCESS',
        payload
    }
};


const fetchLoraOtherConfigsFailureAction = (err) => {
    const payload = err;
    return {
        type: 'FETCH_LORA_OTHER_CONFIGS_FAILURE',
        payload
    }
};


const loraOtherConfigsErrorAction = (payload) => {
    return {
        type: 'LORA_OTHER_CONFIGS_ERROR',
        payload
    }
}


const editLoraOtherConfigsAction = (path, value) => {
	const result = otherLoraConfigsValidator(path, value);

	const payload = {
		data: createObjectByFieldPathfrom(path, result.data),
		isDataValid: createObjectByFieldPathfrom(path, result.isValid)
	}

    return {
        type: 'EDIT_LORA_OTHER_CONFIGS',
        payload
    }
}


const loraOtherConfigsOpenConfirm = (payload) => {
    return {
        type: 'LORA_OTHER_CONFIGS_OPEN_CONFIRM',
        payload
    }
}


const loraOtherConfigsCloseConfirm = () => {
    return {
        type: 'LORA_OTHER_CONFIGS_CLOSE_CONFIRM'
    }
}


const fetchLoraOtherConfigs = (loginToken, ApiService) => (dispatch) => {
    const head = {
        cmd: 'get_lora_other_conf',
        loginToken
    };
    dispatch(fetchLoraOtherConfigsRequestAction());
    ApiService.loraOtherConfigsRequest(head)
        .then((response) => {
			if (response.result) {
				dispatch(fetchLoraOtherConfigsSuccessAction(response.data))
			} else {
				dispatch(fetchLoraOtherConfigsFailureAction(response.msg));
			}
		})
        .catch((er) => {
			console.error("ERROR", er);
            dispatch(fetchLoraOtherConfigsFailureAction('cant_connect_to_server'));
        });
}


const sendLoraOtherConfigs = (loginToken, ApiService, data) => (dispatch) => {
    const head = {
        cmd: 'set_lora_other_conf',
        loginToken
    };
    dispatch(loraOtherConfigsCloseConfirm());
    ApiService.loraOtherConfigsRequest(head, data)
        .then((response) => {
			if (response.result) {
				fetchLoraOtherConfigs(loginToken, ApiService)(dispatch);
			} else {
                console.log(response.data);
				dispatch(loraOtherConfigsErrorAction(response.msg));
			}
		})
        .catch((er) => {
			console.error("ERROR", er);
            dispatch(loraOtherConfigsErrorAction('cant_connect_to_server'));
        });
}



export {
    fetchLoraOtherConfigs,
    sendLoraOtherConfigs,
    editLoraOtherConfigsAction,
    loraOtherConfigsOpenConfirm,
    loraOtherConfigsCloseConfirm
}
