import React from 'react';
import { LoraLogsRequesterConsumer } from "../contexts/lora-logs-requester-context";

/**
 * This function wraps the view component for handling the server interaction
 * over this component.
 *
 * @param {Object} Reducer actions.
 * @param {Component} View component.
 *
 * @returns The component wrapped into the server functions.
 * */
const withLoraLogsRequester = (Component) => {
    return (props) => {
        return (
            <LoraLogsRequesterConsumer>
                {
                    (LoraLogsRequester) => {
                        return (
                            <Component
								 {...props}
								 LoraLogsRequester={LoraLogsRequester}
                            />
                        )
                    }
                }
            </LoraLogsRequesterConsumer>
        );
    }
};

export default withLoraLogsRequester;
