import React from 'react';

import './droping-list.css';

const DropingList = ({
    title,
    list,
    fieldValue,
    fieldName,
    handleChange,
    dataDescription={},
    isActive,
    isValid=true
}) => {


    /*const onEnter = (event) => {
        if(event.key === 'Enter' && isActive) {
            onSubmit();
        };
    }*/

	const { description='', requires='' } = dataDescription;

	const disabled = isActive ? null : {disabled: 'disabled'};
	const divStyle = isActive ? null : {backgroundColor: '#eee', cursor:'not-allowed', color:'#666'};

    const onChange = isActive ? handleChange : () => {};
    const notActiveStyle = isActive ? null : {backgroundColor: '#eee', cursor:'not-allowed', color:'#666'};
    const notValidStyle = isValid ? null : {borderColor:'#f00'};


    const elements = list.map((el)=> {
        return (
            <option value={el.value} key={el.value}>{el.name}</option>
        )
    });


    return (
        <div className="droping-list" style={divStyle}>
            <div className="title">
               {title}:
            </div>
            <div className="input-div">
                <span className="dropdown">
                    <div className="dropdown-menu">
                        <div className="description">
                            {description}
                        </div>
                        <div className="requires">
                            {requires}
                        </div>
                    </div>

                    <select value={fieldValue} onChange={(item) => onChange(fieldName, item.target.value)} {...disabled} style={{...notActiveStyle, ...notValidStyle}}>
                        {elements}
                    </select>

                    <button className="dropdown-toggle info-circle" data-toggle="dropdown">
                        <i className="fa fa-info-circle" aria-hidden="true"></i>
                    </button>
                </span>
            </div>
        </div>
    );
};

export default DropingList;
