const initialLoraLogsStore = {
	dataState: 'EMPTY',
	data: [],
    error: null,
	isAutoLogChecking: false
}

const MAX_LENGTH = 1200;

const loraLogsReducer = (loraLogsStore=initialLoraLogsStore, action={}) => {
	switch (action.type) {

		case 'FETCH_LORA_LOGS_REQUEST': {
			return {
				...loraLogsStore,
                dataState: 'LOADING',
            	data: [],
                error: null
			}
		}

		case 'FETCH_LORA_LOGS_SUCCESS': {
			const { payload } = action;

			let newArray = [
				...loraLogsStore.data,
				...payload
			];
			if (newArray.length > MAX_LENGTH) {
                newArray = newArray.slice( -MAX_LENGTH );
            }

			return {
				...loraLogsStore,
                dataState: 'READY',
            	data: [
                    ...newArray
                ],
                error: null
			}
		}

		case 'FETCH_LORA_LOGS_FAILURE': {
			const { payload } = action;
			return {
				...loraLogsStore,
                dataState: 'ERROR',
            	data: [
                    ...loraLogsStore.data
                ],
                error: payload
			}
		}

        case 'FETCH_LORA_LOGS_REPEATED_REQUEST': {
			return {
				...loraLogsStore,
                dataState: 'REPEATED_LOADING',
            	data: [
                    ...loraLogsStore.data
                ],
                error: null
			}
		}

		case 'TOGGLE_AUTO_FETCH_LORA_LOGS': {
			const { payload } = action;

			return {
				...loraLogsStore,
                isAutoLogChecking: payload.isAutoLogChecking
			}
		}

		default: return initialLoraLogsStore;
	}
}

export default loraLogsReducer;
