import React, { Component } from 'react';

import './confirm-form.css';

import { withLocalization } from '../../../hoc';

class ConfirmForm extends Component {

    componentDidMount() {
        var confirmForm = document.getElementsByClassName('confirm-form');
        confirmForm[0].scrollIntoView(false);
    }

    componentDidUpdate() {
        var confirmForm = document.getElementsByClassName('confirm-form');
        confirmForm[0].scrollIntoView(false);
    }

    render() {
        const { confirmText, onConfirm, onCancel, dictionary: localization } = this.props;

        return (
            <div className = "confirm-form container-fluid">
                <div className="confirm-form-icon">
                    <i className="fa fa-exclamation-triangle" aria-hidden="true"></i>
                </div>
                <div className="confirm-form-text">{confirmText}</div>
                <div className = "btn-group">
                    <button className="btn btn-default" onClick = {onConfirm}>{localization('confirm.confirm')}</button>
                    <button className="btn btn-danger" onClick = {onCancel}>{localization('confirm.cancel')}</button>
                </div>
            </div>
        )
    }
}

export default withLocalization(ConfirmForm);
