import React from 'react';

import BlockingCurtain from '../../../common/blocking-curtain';
import Spinner from '../../../common/spinner';

import './layout.css';

const Layout = ({
    label,
    text,
    hasSpinner
}) => {
    const spinner = hasSpinner ? <Spinner /> : null;
    return (
        <BlockingCurtain>
            <div className="pinging-server-curtain-layout">
                <div className="pc-label">{label}</div>
                <div className="text">{text}</div>
                {spinner}
            </div>
        </BlockingCurtain>
    )
};

export default Layout;
