import React from 'react';
import { connect } from 'react-redux';
import compose from '../../../../utils/compose';

import { withApiService, withLocalization } from '../../../hoc';

import Spinner from '../../../views/common/spinner';

import {
    rebootDeviceAction,
    rebootDashboardAction,
    shutdownDashboardAction,
    setFactorySettingsAction,
    setFactoryLoraSettingsAction,
    updateServerAction,
    mainOpenConfirm,
    mainCloseConfirm,
    fetchNetworkConfigs
} from '../../../../actions';

import ConfirmForm from '../../../views/common/confirm-form';

import DeviceActionsLayout from '../../../views/pages/device-actions/layout';
import DeviceActionsButton from '../../../views/pages/device-actions/button';

const DeviseActions = ({
    loginToken,
    expertMode,
    isNeedConfirm ,confirmText, confirmAction,
    deviceActionsError,
    networkDataState,

    rebootDeviceAction,
    rebootDashboardAction,
    shutdownDashboardAction,
    setFactorySettingsAction,
    setFactoryLoraSettingsAction,
    updateServerAction,
    mainOpenConfirm,
    mainCloseConfirm,
    fetchNetworkConfigs,

    localization,
    ApiService
}) => {
    const { deviceActions: deviceActionsLocal = {} } = localization;

    const rebootDeviseConfirmData = {
        confirmText: deviceActionsLocal.rebootDevice.confirmReqest,
        confirmAction: () => rebootDeviceAction(loginToken, ApiService, localization)
    };
    const rebootDashboardConfirmData = {
        confirmText: deviceActionsLocal.rebootBsDashboard.confirmReqest,
        confirmAction: () => rebootDashboardAction(loginToken, ApiService, localization)
    };
    const shutdownDashboardConfirmData = {
        confirmText: deviceActionsLocal.shutdownBsDashboard.confirmReqest,
        confirmAction: () => shutdownDashboardAction(loginToken, ApiService, localization)
    };
    const setFactorySettingsConfirmData = {
        confirmText: deviceActionsLocal.resetAllSettings.confirmReqest,
        confirmAction: () => setFactorySettingsAction(loginToken, ApiService, localization)
    };
    const setFactoryLoRaConfirmData = {
        confirmText: deviceActionsLocal.resetLoraSettings.confirmReqest,
        confirmAction: () => setFactoryLoraSettingsAction(loginToken, ApiService, localization)
    };

    const confirmer = isNeedConfirm ? <ConfirmForm confirmText={confirmText} onConfirm={confirmAction} onCancel={()=>mainCloseConfirm()}/> : null;

    let deviceActionsButtons = [
        <DeviceActionsButton
            key="1"
            label={deviceActionsLocal.rebootDevice.title}
            description={deviceActionsLocal.rebootDevice.description}
            action={()=>mainOpenConfirm(rebootDeviseConfirmData)}
        />
    ];

    let dashboardActionsButtons = [
        <DeviceActionsButton
            key="1"
            label={deviceActionsLocal.rebootBsDashboard.title}
            description={deviceActionsLocal.rebootBsDashboard.description}
            action={()=>mainOpenConfirm(rebootDashboardConfirmData)}
        />
        ,
        <DeviceActionsButton
            key="2"
            label={deviceActionsLocal.shutdownBsDashboard.title}
            description={deviceActionsLocal.shutdownBsDashboard.description}
            action={()=>mainOpenConfirm(shutdownDashboardConfirmData)}
        />
    ];

    let filesActionsButtons = [
        <DeviceActionsButton
            key="2"
            label={deviceActionsLocal.resetLoraSettings.title}
            description={deviceActionsLocal.resetLoraSettings.description}
            action={()=>mainOpenConfirm(setFactoryLoRaConfirmData)}
        />
        ,
        <DeviceActionsButton
            key="1"
            label={deviceActionsLocal.resetAllSettings.title}
            description={deviceActionsLocal.resetAllSettings.description}
            action={()=>mainOpenConfirm(setFactorySettingsConfirmData)}
        />
    ];

    if (isNeedConfirm) {
        deviceActionsButtons = null;
        dashboardActionsButtons = null;
        filesActionsButtons = null;
    }

    let content = null;
    switch (networkDataState) {
        case 'EMPTY': {
            fetchNetworkConfigs(loginToken, ApiService);
            content = <Spinner />
            break;
        }
        case 'LOADING': {
            content = <Spinner />
            break;
        }
        default: {
            content = (
                <DeviceActionsLayout
                    localization={deviceActionsLocal}
                    title={deviceActionsLocal.title}
                    deviceActionsButtons={deviceActionsButtons}
                    dashboardActionsButtons={expertMode ? dashboardActionsButtons : null}
                    filesActionsButtons={filesActionsButtons}
                    confirmer={confirmer}
                    error={deviceActionsError}
                />
            )
        }
    }

    return content;
}

const mapStoreToProps = ({loginToken, expertMode, isNeedConfirm ,confirmText, confirmAction, network_conf, deviceActionsError}) => {
    const { dataState } = network_conf;
    return {
        loginToken,
        expertMode,
        deviceActionsError,
        networkDataState: dataState,
        isNeedConfirm ,confirmText, confirmAction
    }
}

const mapDispatchToProps = {
    rebootDeviceAction,
    rebootDashboardAction,
    shutdownDashboardAction,
    setFactorySettingsAction,
    setFactoryLoraSettingsAction,
    updateServerAction,
    mainOpenConfirm,
    mainCloseConfirm,
    fetchNetworkConfigs
}

export default compose(
    connect(mapStoreToProps, mapDispatchToProps),
    withApiService,
    withLocalization
)(DeviseActions);
