import React from 'react';

import './text-label.css'

const TextLabel = ({
	title,
	dataDescription = {},
	fieldName,
	fieldValue,
	currentFieldValue,
	isValid,
	notValidDescription,
	onChangeLabel,
	active=true,
	onSubmit = () => {}
}) => {

	const onEnter = (event) => {
        if(event.key === 'Enter' && active) {
            onSubmit();
        };
    }

	const { description='', requires='' } = dataDescription;

	const disabled = active ? null : {disabled: 'disabled'};
	const divStyle = active ? null : {backgroundColor: '#eee', cursor:'not-allowed', color:'#666'};
	const inputStyle = active ? null : {backgroundColor: '#eee', cursor:'not-allowed'};
	const inputValidStyle = isValid ? null: {boxShadow:'0 0 2px 1px #f55', borderColor: '#f00'};

	return (
		<div className="text-label" style={divStyle}>
			<div className="title">
				{title}:
			</div>
			<div className="input-div">
				<span className="dropdown">
					<div className="dropdown-menu">
						<div className="description">
							{description}
						</div>
						<div className="requires">
							{requires}
						</div>
					</div>

					<input
						{...disabled}
							style={{...inputStyle, ...inputValidStyle}}
						type="text"
						value={fieldValue}
						onChange={(item) => onChangeLabel(fieldName, item.target.value)}
						onKeyPress={onEnter}
					/>
					<button className="dropdown-toggle info-circle" data-toggle="dropdown">
						<i className="fa fa-info-circle" aria-hidden="true"></i>
					</button>
				</span>

			</div>
		</div>
	)
}

export default TextLabel;
