import { requestEncryption } from '../utils/crypto';

class ApiService {
    // TODO:
    _apiBase = /*'192.168.1.228';*/window.location.hostname;
    _port = 3001;


    getConnectData = () => {
        return {
            host: this._apiBase,
            port: this._port
        }
    }


    setConnectData = ({host, port}) => {
        this._apiBase = host;
        this._port = port;
    }


    postRequester = async (head={}, data={}, path) => {
        const request = {
            ...head,
            data
        };
        const address = `http://${this._apiBase}:${this._port}${path}`;

        const result = await fetch(address,
           {
               method: 'POST',
               body:JSON.stringify(request),
               headers: {'Content-Type': 'application/json'}
           });

       if (!result.ok) {
           throw new Error(`could not fetch ${address}, received ${result.status}`);
       }

       return await result.json();
   }


    getRequester = async (path) => {
       const address = `http://${this._apiBase}:${this._port}${path}`;
       const result = await fetch(address,
          {
              method: 'GET'
          });
      if (!result.ok) {
          throw new Error(`could not fetch ${address}, received ${result.status}`);
      }

      return await result.json();
    }

    stateRequest = async (head) => {
        return await this.postRequester(head, {}, '/get-state');
    }

    pingRequest = async () => {
        return await this.getRequester('/ping');
    }

    initialRequest = async (head, data) => {
        return await this.postRequester(head, data, '/initial');
    }

    loginRequest = async (head, loginData) => {
        const data = requestEncryption(loginData);
        return await this.postRequester(head, data, '/authorization');
    }

    globalConfigsRequest = async (head, data) => {
        return await this.postRequester(head, data, '/global-configs');
    }

    networkConfigsRequest = async (head, data) => {
        return await this.postRequester(head, data, '/network-configs');
    }

    configs3GRequest = async (head, data) => {
        return await this.postRequester(head, data, '/3g');
    }

    frequencyConfigsRequest = async (head, data) => {
        return await this.postRequester(head, data, '/frequency-configs');
    }

    frequencyPresetsRequest = async (head, data) => {
        return await this.postRequester(head, data, '/frequency-configs');
    }

    loraGpsConfigsRequest = async (head, data) => {
        return await this.postRequester(head, data, '/lora-gps-configs');
    }

    loraOtherConfigsRequest = async (head, data) => {
        return await this.postRequester(head, data, '/lora-other-configs');
    }

    loraLogsRequest = async (head) => {
        return await this.postRequester(head, undefined, '/lora-logs');
    }

    aboutInfoRequest = async (head) => {
        return await this.postRequester(head, undefined, '/about');
    }

    appSettingsRequest = async (head, data) => {
        return await this.postRequester(head, data, '/app-settings');
    }

    appDeviceActionsRequest = async (head, data) => {
        return await this.postRequester(head, data, '/device-actions');
    }
}

export default ApiService;
