import React, { Component } from 'react';

import './log-area.css';

class LoraLogArea extends Component {
    componentDidMount() {
        // Scroll list down
        var block = document.getElementById("id-lora-log-area");
        block.scrollTop = block.scrollHeight;
    }

    componentDidUpdate() {
        const {
            isScrolling
        } = this.props;
        // Scroll list down
        if (isScrolling) {
            const block = document.getElementById("id-lora-log-area");
            block.scrollTop = block.scrollHeight;
        }
    }

    render () {
        const {
            children,
            height
        } = this.props;

        const rows = children.map((el)=> {
            return <LogRow el={el} key={el.rowId}/>
        })

        const windowHeight = (height - 350) < 100 ? 100 : height - 350;
        const style = {height: windowHeight + 'px'};

        return (
            <pre className="lora-log-area" id="id-lora-log-area" style={style}>
                {rows}
            </pre>
        )
    }
}

const LogRow = ({el}) => {
    return (
        <span>
            {el.row}
        </span>
    )
}

export default LoraLogArea;
