import React from 'react';
import { connect } from 'react-redux';

import { withApiService, withLocalization } from '../../../hoc';

import { hideUpdateLoading, startWaitingForServerToLoad } from '../../../../actions';

import Layout from '../../../views/specific/update-curtain/layout';
import Row from '../../../views/specific/update-curtain/row';

class UpdateCurtain extends React.Component {

    constructor() {
        super();

        this.state = {
            rows: [],
            updateState: 'IN_PROCESS'
        }

    }


    requestServer = () => {
        const { loginToken, ApiService, hideUpdateLoading, startWaitingForServerToLoad } = this.props;

        ApiService.stateRequest({loginToken, cmd: 'get_update_state'})
            .then((response) => {
                if (response.result) {
                    // process. Fetch update state
                    const { data } = response;
                    const {updateState, stageDescription} = data;

                    switch (updateState) {
                        case 'IN_PROCESS': {
                            setTimeout(()=>{
                                this.requestServer();
                            }, 5000);
                            this.setState({rows: [...stageDescription]})
                            break;
                        }
                        case 'INSTALLED': {
                            console.log('update completed!');
                            setTimeout(()=>{
                                this.requestServer();
                            }, 5000);
                            this.setState({rows: [...stageDescription], updateState})
                            break;
                        }
                        case 'FAILURE': {
                            console.error('update failure!', stageDescription);
                            this.setState({rows: [...stageDescription], updateState})
                            break;
                        }
                        default: {
                            setTimeout(()=>{
                                this.requestServer();
                            }, 5000);
                        }
                    }


                } else {
                    //alert('Update request ERROR' + response.msg);
                    hideUpdateLoading();
                    startWaitingForServerToLoad('resultNotices.updateComplete');
                }
            })
            .catch(() => {
                hideUpdateLoading();
                startWaitingForServerToLoad('resultNotices.updateComplete');
            })
    }


    componentDidMount () {
        this.requestServer();
    }


    render () {
        const { dictionary } = this.props;
        const {updateState, rows} = this.state;

        const updateLabel = dictionary('update.' + updateState);
        const additionalLabel = updateState === 'FAILURE' ? dictionary('update.TRY_LATER') : null;

        let i = 0;
        const rowsElems = rows.map((el)=>{
            if (typeof el === 'object') {
                return <Row key={i++}><span className='failure'>{dictionary('update.FAILURE')}</span></Row>
            }
            return <Row key={i++}>{dictionary('update.' + el)}</Row>
        })

        return (
            <Layout updateState={updateState} label={updateLabel} rows={rowsElems} additionalLabel={additionalLabel} />
        )
    }

}

const mapStoreToProps = ({ loginToken }) => {
    return {
        loginToken
    }
}

const mapDispatchToProps = { hideUpdateLoading, startWaitingForServerToLoad };

export default connect(mapStoreToProps, mapDispatchToProps)(withApiService(withLocalization(UpdateCurtain)));
