import React from 'react';

import SmallSpinner from '../../../common/small-spinner';

import './frequency-expert-presets-form.css';

const FrequencyExpertPresetsForm = ({ presetElementsArray }) => {
    const presetElements = presetElementsArray.map((el) => {
        const {
            name,
            active,
            itemState,
            isSelected,
            isCurrent,
            description,
            onSelect,
            errorText,
        } = el;
        const selectedStyle = isSelected ? 'selected-style' : 'not-selected-style';
        const currentStyle = isCurrent ? 'current-style' : 'not-current-style';

        let onClick = onSelect;
        let leftComponent = null;
        let itemStyle = null;
        switch (itemState) {
            case 'EMPTY': {
                leftComponent = <SmallSpinner hold/>;
                break;
            }
            case 'ERROR': {
                itemStyle = {
                    backgroundColor: '#f88',
                    color: "#a00",
                    borderColor: "#a00",
                    cursor: "not-allowed"
                }
                onClick = () => {};
                leftComponent = <ErrorInfo errorText={errorText}/>;
                break;
            }
            case 'READY': {
                break;
            }
            case 'LOADING': {
                leftComponent = <SmallSpinner />;
                break;
            }
            default: {
            }
        }

        const elementDisabledStyle = !active && !isCurrent ? {backgroundColor: '#eee', cursor:'not-allowed'} : null;
        const elementButtonDisabledStyle = !active && !isSelected ? {backgroundColor: '#eee', borderColor: '#aaa', color: '#666', cursor:'not-allowed'} : null;
        onClick = active ? onClick : () => {};

        return (
            <div className="col-sm-3 form-panel" key={name}>
                <div className={`frequency-expert-presets-element ${currentStyle}`} title={description} style={{...elementDisabledStyle}}>
                    {leftComponent}
                    <button className={`frequency-expert-presets-element-button ${selectedStyle}`} onClick={onClick} style={{...elementButtonDisabledStyle, ...itemStyle}}>
                        {name}
                    </button>
                </div>
            </div>
        )
    })

    return (
        <div className="page-form frequency-expert-presets-form">

            {presetElements}
            <div className="frequency-expert-presets-form-line container-fluid"></div>
        </div>
    )
};

const ErrorInfo = ({ errorText = 'error' }) => {
    return (
        <span className="dropdown">
            <div className="dropdown-menu">
                {errorText}
            </div>

            <button className="dropdown-toggle info-error" data-toggle="dropdown">
                <i className="fa fa-exclamation-triangle" aria-hidden="true"></i>
            </button>
        </span>
    )
}

const FileActionsButtons = ({ onSavePresetFile, onOpenPresetFile, localization, active }) => {
    const { loraGlobalFrequency } = localization;
    const elementButtonDisabledStyle = !active ? {backgroundColor: '#eee', borderColor: '#aaa', color: '#666', cursor:'not-allowed', boxShadow: 'none'} : null;
    const onSavePresetFileAction = active ? onSavePresetFile : () => {};
    const onOpenPresetFileAction = active ? onOpenPresetFile : () => {};
    return (
        <div className="file-actions-buttons btn-group">
                <button className="btn btn-default" onClick={onSavePresetFileAction} style={elementButtonDisabledStyle}>
                    {loraGlobalFrequency.buttons.savePresetToFile}
                    <i className="fa fa-floppy-o" aria-hidden="true"></i>
                </button>

                <button className="btn btn-default" onClick={onOpenPresetFileAction} style={elementButtonDisabledStyle}>
                    {loraGlobalFrequency.buttons.loadPresetFromFile}
                    <i className="fa fa-folder-open-o" aria-hidden="true"></i>
                </button>

        </div>
    )
}

export {
    FrequencyExpertPresetsForm,
    FileActionsButtons
}
