import React from 'react';

import LightTextLabel from '../../../common/light-text-label';
import LightDropingList from '../../../common/light-droping-list';

import './manager-panel.css';

const AppSettingsManagerPanel = ({
    isExpertMode,
    data,
    onChange,
    onSubmit,
    isValid,
    isActive,
    localization,
    localizationPrefix
}) => {
    const {
        BS_DASHBOARD = {},
        BS_DASHBOARD_MANAGER = {},
        UPDATE = {}
    } = data;

    const expertModeFields = isExpertMode ? (
        <React.Fragment>
            <div className="group-title col-md-12">
                {localization(localizationPrefix + 'dashboardUpdateSources')}:
            </div>
            {
                fetchFieldsArr(
                    BS_DASHBOARD,
                    isValid.BS_DASHBOARD,
                    onChange('BS_DASHBOARD'),
                    onSubmit,
                    isActive,
                    localization,
                    localizationPrefix
                )
            }

            <div className="group-title col-md-12">
                {localization(localizationPrefix + 'managerUpdateSources')}:
            </div>
            {
                fetchFieldsArr(
                    BS_DASHBOARD_MANAGER,
                    isValid.BS_DASHBOARD_MANAGER,
                    onChange('BS_DASHBOARD_MANAGER'),
                    onSubmit,
                    isActive,
                    localization,
                    localizationPrefix
                )
            }
        </React.Fragment>
    ) : null;

    return (
        <div className="app-settings-manager-panel page-list">

            <div className="group-title col-md-12">
                {localization(localizationPrefix + 'UPDATE')}:
            </div>
            {
                fetchFieldsArr(
                    UPDATE,
                    isValid.UPDATE,
                    onChange('UPDATE'),
                    onSubmit,
                    isActive,
                    localization,
                    localizationPrefix
                )
            }


            {expertModeFields}


            <div className="group-title col-md-12">
                {localization(localizationPrefix + 'other')}:
            </div>
            <div className="container-fluid item">
                <div className="item-title col-md-3">
                    {localization(localizationPrefix + 'SERVER_STARTUP_METHOD.title')}
                </div>
                <div className="item-value col-md-9">
                    <LightDropingList
                        value={data.SERVER_STARTUP_METHOD}
                        isActive={isActive}
                        isValid={isValid.SERVER_STARTUP_METHOD}
                        description={localization(localizationPrefix + 'SERVER_STARTUP_METHOD.description')}
                        handleChange={onChange()('SERVER_STARTUP_METHOD')}
                        list={[
                            {name: localization(localizationPrefix + 'SERVER_STARTUP_METHOD.values.button'), value: 'button'},
                            {name: localization(localizationPrefix + 'SERVER_STARTUP_METHOD.values.automatically'), value: 'automatically'}
                        ]}
                    />
                </div>
            </div>

            <div className="container-fluid item">
                <div className="item-title col-md-3">
                    {localization(localizationPrefix + 'loggerLevel.title')}
                </div>
                <div className="item-value col-md-9">
                    <LightDropingList
                        value={data.loggerLevel}
                        isActive={isActive}
                        isValid={isValid.loggerLevel}
                        description={localization(localizationPrefix + 'loggerLevel.description')}
                        handleChange={onChange()('loggerLevel')}
                        list={[
                            {name: localization(localizationPrefix + 'loggerLevel.values.error'), value: 'error'},
                            {name: localization(localizationPrefix + 'loggerLevel.values.warn'), value: 'warn'},
                            {name: localization(localizationPrefix + 'loggerLevel.values.info'), value: 'info'},
                            {name: localization(localizationPrefix + 'loggerLevel.values.verbose'), value: 'verbose'},
                            {name: localization(localizationPrefix + 'loggerLevel.values.debug'), value: 'debug'},
                            {name: localization(localizationPrefix + 'loggerLevel.values.silly'), value: 'silly'}
                        ]}
                    />
                </div>
            </div>
        </div>
    )
}

const field = (name, value, isValid, onChange, onSubmit, isActive, localization, localizationPrefix) => {
    let fieldInput = null;
    if (name === 'GIT_PROVIDER') {
        fieldInput = <LightDropingList
            value={value}
            isActive={isActive}
            description={localization(localizationPrefix + name + '.description')}
            handleChange={onChange}
            list={[{name: localization(localizationPrefix + name + '.values.GIT_HUB'), value: 'GIT_HUB'}, {name: localization(localizationPrefix + name + '.values.GIT_LAB'), value: 'GIT_LAB'}]}
        />
    } else {
        fieldInput = <LightTextLabel
            value={value}
            isActive={isActive}
            onChange={onChange}
            description={localization(localizationPrefix + name + '.description')}
            isValid={isValid}
            onSubmit={onSubmit}
        />
    }
    return (
        <div className="container-fluid item" key={name}>
            <div className="item-title col-md-3">
                {localization(localizationPrefix + name + '.title')}
            </div>
            <div className="item-value col-md-9">
                {fieldInput}
            </div>
        </div>
    )
}

const fetchFieldsArr = (obj, isValid, onChange, onSubmit, isActive, localization, localizationPrefix) => {
    let arr = [];
    for (let key in obj) {
        arr = [
            ...arr,
            field(key, obj[key], isValid[key], onChange(key), onSubmit, isActive, localization, localizationPrefix)
        ]
    }
    return arr;
}

export default AppSettingsManagerPanel;
