import React from 'react';
import { connect } from 'react-redux';
import compose from '../../../../utils/compose';

import { isObjectValid } from '../../../../utils/page-data-validators'
import { withApiService, withLocalization } from '../../../hoc';

import {
	fetchLoraOtherConfigs,
	sendLoraOtherConfigs,
    editLoraOtherConfigsAction,
	loraOtherConfigsOpenConfirm,
    loraOtherConfigsCloseConfirm
} from '../../../../actions';

import Spinner from '../../../views/common/spinner';
import ConfirmForm from '../../../views/common/confirm-form';

import Layout from '../../../views/pages/other-lora-configs/layout';
import Form from '../../../views/pages/other-lora-configs/form';
import Buttons from '../../../views/pages/other-lora-configs/buttons';


const OtherLoraConfigs = ({
    loginToken,

    data,
    currentData,
    dataState,
    isDataValid,
	isNeedConfirm,
	confirmText,
	confirmAction,
    error,

    fetchLoraOtherConfigs,
	sendLoraOtherConfigs,
    editLoraOtherConfigsAction,
	loraOtherConfigsOpenConfirm,
    loraOtherConfigsCloseConfirm,

    ApiService,
    dictionary: localization
}) => {

	const onSetConfigsConfirmData = {
		confirmText: localization("otherLoraConfigs.confirmReqests.onSave"),
		confirmAction: () => sendLoraOtherConfigs(loginToken, ApiService, data)
	}
	const onReRequestConfirmData = {
		confirmText: localization('otherLoraConfigs.confirmReqests.onReRequest'),
		confirmAction: () => fetchLoraOtherConfigs(loginToken, ApiService)
	}

    const onChangeLabel = (field, value) => {
        editLoraOtherConfigsAction(field, value);
    }

	const sendDataAction = () => {
		if (isObjectValid(isDataValid)) {
			loraOtherConfigsOpenConfirm(onSetConfigsConfirmData);
		} else {
			alert(localization.confirm.notValidFields);
		}
	}

    let content = null;
    switch (dataState) {
        case 'EMPTY': {
            fetchLoraOtherConfigs(loginToken, ApiService);
            break;
        }
        case 'LOADING': {
            content = <Spinner />
            break;
        }
        case 'READY': {
            content = <Form
				localization={localization}
				localizationPrefix="otherLoraConfigs."

                data={data}
                currentData={currentData}
                isDataValid={isDataValid}
                onChangeLabel={onChangeLabel}
                onPressEnter={sendDataAction}

                isActive={!isNeedConfirm}
            />
            break;
        }
        case 'ERROR': {
            content = null
            break;
        }
        default: content = null
    }

    let buttons = null;
	if (isNeedConfirm) {
		buttons = <ConfirmForm confirmText={confirmText} onConfirm={confirmAction} onCancel={()=>loraOtherConfigsCloseConfirm()}/>;
	} else {
		buttons = <Buttons
			localization={localization}
			localizationPrefix="otherLoraConfigs."

			dataState={dataState}
			sendData={sendDataAction}
		    reRequestData={()=>loraOtherConfigsOpenConfirm(onReRequestConfirmData)}
		/>;
	}

    return (
        <Layout
            title={localization("otherLoraConfigs.title")}
            content={content}
            error={error}
            buttons={buttons}
        />
    )
};


const mapStoreToProps = ({loginToken, otherLoraConfigs}) => {
    const {
        data,
        currentData,
        dataState,
        isDataValid,
		isNeedConfirm,
		confirmText,
		confirmAction,

        error
    } = otherLoraConfigs;
    return {
        loginToken,

        data,
        currentData,
        dataState,
        isDataValid,
		isNeedConfirm,
		confirmText,
		confirmAction,

        error
    }
}


const mapDispatchToProps = {
    fetchLoraOtherConfigs,
	sendLoraOtherConfigs,
    editLoraOtherConfigsAction,
	loraOtherConfigsOpenConfirm,
    loraOtherConfigsCloseConfirm
}


export default compose(
    connect(mapStoreToProps, mapDispatchToProps),
    withApiService,
    withLocalization
)(OtherLoraConfigs);
