
const gpsLoraConfigsValidator = (field, value) => {
    switch (field) {

        case 'use_gps': {
            return {
                data: value,
                isValid: (value === 'enabled' || value === 'disabled')
            }
        }



        case 'fake_gps': {
            let newValue = value;
            switch (value) {
                case 'true': {
                    newValue = true;
                    break;
                }
                case 'false': {
                    newValue = false;
                    break;
                }
                default: {}
            }
            return {
                data: newValue,
                isValid: (typeof newValue === 'boolean')
            }
        }



        case 'ref_latitude':
        case 'ref_longitude': {
            let newValue = ('' + value).substr(0, 10);
            newValue = newValue.split(',').join('.');
            const re = /^[-]?[0-9]{1,3}([.]{1,1}[0-9]{1,5})?$/;
            return {
                data: newValue,
                isValid: re.test(newValue)
            };
        }



        case 'ref_altitude': {
            let newValue = ('' + value).substr(0, 7);
            newValue = newValue.split(',').join('.');
            const re = /^[-]?[0-9]{1,6}$/;
            return {
                data: newValue,
                isValid: re.test(newValue)
            };
        }



        default: {
            return {data: value, isValid: false};
        }

    }
}

export default gpsLoraConfigsValidator;
