import { deepMap } from '../utils/objects-utils/deep-map-obj';
import createObjectByFieldPathfrom from '../utils/objects-utils/create-object-by-field-path';
import gpsLoraConfigsValidator from '../utils/validators/gps-lora-configs-validator';

const fetchLoraGpsConfigsRequestAction = () => {
    return {
        type: 'FETCH_LORA_GPS_CONFIGS_REQUEST'
    }
};


const fetchLoraGpsConfigsSuccessAction = (data) => {
    const isValid = deepMap(data, (key, val, path) => {
		const result = gpsLoraConfigsValidator(key, val).isValid;
		return (result);
	});

	const payload = {
		data,
		isValid
	}

    return {
        type: 'FETCH_LORA_GPS_CONFIGS_SUCCESS',
        payload
    }
};


const fetchLoraGpsConfigsFailureAction = (err) => {
    const payload = err;
    return {
        type: 'FETCH_LORA_GPS_CONFIGS_FAILURE',
        payload
    }
};


const loraGpsConfigsErrorAction = (payload) => {
    return {
        type: 'LORA_GPS_CONFIGS_ERROR',
        payload
    }
}


const editGpsLoraConfigsAction = (path, value) => {
	const result = gpsLoraConfigsValidator(path, value);

	const payload = {
		data: createObjectByFieldPathfrom('Object.'+path, result.data),
		isDataValid: createObjectByFieldPathfrom('Object.'+path, result.isValid)
	}

    return {
        type: 'EDIT_LORA_GPS_CONFIGS',
        payload
    }
}


const gpsLoraConfigsOpenConfirm = (payload) => {
    return {
        type: 'LORA_GPS_CONFIGS_OPEN_CONFIRM',
        payload
    }
}


const gpsLoraConfigsCloseConfirm = () => {
    return {
        type: 'LORA_GPS_CONFIGS_CLOSE_CONFIRM'
    }
}


const fetchGpsLoraConfigs = (loginToken, ApiService) => (dispatch) => {
    const head = {
        cmd: 'get_lora_gps_conf',
        loginToken
    };
    dispatch(fetchLoraGpsConfigsRequestAction());
    ApiService.loraGpsConfigsRequest(head)
        .then((response) => {
			if (response.result) {
                const dataForFetch = {
                    use_gps: response.data.use_gps,
                    fake_gps: response.data.fake_gps,
                    ref_latitude: '' + response.data.ref_latitude,
                    ref_longitude: '' + response.data.ref_longitude,
                    ref_altitude: '' + response.data.ref_altitude
                }
				dispatch(fetchLoraGpsConfigsSuccessAction(dataForFetch))
			} else {
				dispatch(fetchLoraGpsConfigsFailureAction(response.msg));
			}
		})
        .catch((er) => {
			console.error("ERROR", er);
            dispatch(fetchLoraGpsConfigsFailureAction('cant_connect_to_server'));
        });
}


const sendGpsLoraConfigs = (loginToken, ApiService, data) => (dispatch) => {
    const head = {
        cmd: 'set_lora_gps_conf',
        loginToken
    };

    const dataForSend = {
        use_gps: data.use_gps,
        fake_gps: data.fake_gps,
        ref_latitude: parseFloat(data.ref_latitude),
        ref_longitude: parseFloat(data.ref_longitude),
        ref_altitude: parseFloat(data.ref_altitude)
    }

    dispatch(gpsLoraConfigsCloseConfirm());
    ApiService.loraGpsConfigsRequest(head, dataForSend)
        .then((response) => {
			if (response.result) {
				fetchGpsLoraConfigs(loginToken, ApiService)(dispatch);
			} else {
                console.log(response.data);
				dispatch(loraGpsConfigsErrorAction(response.msg));
			}
		})
        .catch((er) => {
			console.error("ERROR", er);
            dispatch(loraGpsConfigsErrorAction('cant_connect_to_server'));
        });
}



export {
    fetchGpsLoraConfigs,
    sendGpsLoraConfigs,
    editGpsLoraConfigsAction,
    gpsLoraConfigsOpenConfirm,
    gpsLoraConfigsCloseConfirm
}
