import React from 'react';
import { connect } from 'react-redux';
import compose from '../../../utils/compose';
import { withApiService, withLoraLogsRequester } from '../../hoc';

import { authorizationLogout } from '../../../actions';

import LogoutForm from '../../views/specific/logout-form';

const Logout = ({ loginToken, LoraLogsRequester, ApiService, authorizationLogout }) => {

    const onLogout = () => {
        const head = {
            cmd: 'logout_request'
        };
        authorizationLogout();

        LoraLogsRequester.stopRequesting();
        ApiService.loginRequest(head, {loginToken}).then((response) => {

        })
        .catch((er) => {
            console.log("ERROR - logoutRequest", er);
        })
    }

    return (
        <LogoutForm onLogout={onLogout} />
    )
}

const mapStoreToProps = ({ loginToken }) => {
    return {
        loginToken
    }
};

const mapDispatchToProps = (dispatch) => {
	return {
        authorizationLogout: (token) => dispatch(authorizationLogout(token))
	}
}

export default compose(
    connect(mapStoreToProps, mapDispatchToProps),
    withApiService,
    withLoraLogsRequester
)(Logout);
