import React from 'react';

import './buttons.css';

const Buttons = ({
    sendData,
    reRequestData,
    dataState,
    localization,
    localizationPrefix
}) => {
    const sendButton = (dataState === 'READY') ? <li><button className="btn btn-success" onClick={sendData}>{localization(localizationPrefix+"buttons.save")}</button></li> : null;
    return (
        <div className="bottom-buttons gps-lora-configs-buttons">
            <nav className="navbar" role="navigation">
                <div className="container-fluid">
                    <ul className="nav navbar-nav">
                        {sendButton}
                        <li><button className="btn btn-default" onClick={reRequestData}>{localization(localizationPrefix+"buttons.reRequest")}</button></li>
                    </ul>
                </div>
            </nav>
        </div>
    )
};

export default Buttons;
