import React from 'react';
import { connect } from 'react-redux';

import { withApiService, withLocalization } from '../../../hoc';

import Layout from '../../../views/specific/pinging-server-curtain/layout';

const PingingServerCurtain = ({
    dictionary,
    ApiService,
    waitingForServerToLoadDescription,
    address,
    eth0,
    loginToken,
}) => {
    let text;
    let hasSpinner = false;

    /*
    *   When reboot device
    */
    if (waitingForServerToLoadDescription === 'resultNotices.rebootAccepted') {
        switch (eth0) {
            case 'dhcp': {
                text = dictionary('resultNotices.ip_address_is_not_static');
                break;
            }
            case 'static': {
                ApiService.setConnectData({host: address, port: 3001})
                const pingServer = () => {
                    ApiService.pingRequest()
                        .then((response) => {
                            // Refresh page
                            document.location.replace('http://' + address);
                        })
                        .catch(() => {
                            setTimeout(pingServer, 5000);
                        })
                }

                pingServer();
                text = dictionary('resultNotices.wait_reload_server');
                hasSpinner = true;
                break;
            }
            default: {
                text = dictionary('resultNotices.failed_get_current_network_data');
            }
        }
    /*
    *   When not reboot device
    */
    } else {
        const pingServer = () => {
            ApiService.pingRequest()
                .then((response) => {
                    // Refresh page
                    document.location.replace('/');
                })
                .catch(() => {
                    setTimeout(pingServer, 5000);
                })
        }

        pingServer();
        text = dictionary('resultNotices.wait_reload_server');
        hasSpinner = true;
    }


    return (
        <Layout label={dictionary(waitingForServerToLoadDescription)} text={text} hasSpinner={hasSpinner}/>
    )
}

const mapStoreToProps = ({ waitingForServerToLoadDescription, network_conf, loginToken }) => {
    const { currentData = {} } = network_conf;
    const { address, eth0 } = currentData;
    return {
        waitingForServerToLoadDescription,
        address,
        eth0,
        loginToken
    }
}

export default connect(mapStoreToProps)(withLocalization(withApiService(PingingServerCurtain)));
